import { createStore } from 'vuex'

// Загружаем сохраненное состояние из localStorage
const savedState = localStorage.getItem('vuexState')
const initialState = savedState ? JSON.parse(savedState) : { tasks: [] }

export default createStore({
  state: initialState, // Если что-то есть в localStorage
  mutations: {
    ADD_TASK(state, task) {
      state.tasks.push(task)
    },
    DELETE_TASK(state, index) {
      state.tasks.splice(index, 1)
    },
    EDIT_TASK(state, { title, index }) {
      state.tasks[index] = { title }
    },
    SAVE_STATE(state) {
      localStorage.setItem('vuexState', JSON.stringify(state))
    },
  },
  actions: {
    addTask({ commit, dispatch }, title) {
      commit('ADD_TASK', { title })
      dispatch('saveState')
    },
    deleteTask({ commit, dispatch }, index) {
      commit('DELETE_TASK', index)
      dispatch('saveState')
    },
    editTask({ commit, dispatch }, { title, index }) {
      commit('EDIT_TASK', { title, index })
      dispatch('saveState')
    },
    saveState({ commit }) {
      commit('SAVE_STATE')
    },
  },
})